				<span class="chip__label chip__label--mt">
					Стандартный номер с французским балконом.<br>В декоре использованы материалы, обладающие естественной природной фактурой. Прохлада камня, мягкость натуральных тканей и фактурность дерева создают уникальный тактильный интерьер.
