	<info-popup class="checkIn" :class="pickDate ? 'pickDate' : '' " :show="popups[7]" @closeInfoPopup="closeInfoPopup(7)">
		<template v-slot:header>
			<p v-if="!sendDate || !pickDate">Изменение дат проживания</p>
			<p v-else-if="pickDate">Выберите дату заезда</p>
			<p v-else></p>
		</template>

		<template v-slot:content>
			<div v-if="!sendDate">
				<v-date-picker
					v-model="dateStart"
					is-range
					:popover='popoverConfig'
					:update-on-input="false"
					@click="checkDate"
				>
					<template v-slot="{ inputValue, inputEvents }">
						<label>
							<input
								class="form-control form-control--checkIn"
								:value="inputValue.start"
								v-on="inputEvents.start"
								@click="pickDate = true"
							/>
							<span class="chip__label popup__input-text">Заезд</span>
						</label>

						<label>
							<input
								class="form-control form-control--checkIn"
								:value="inputValue.end"
								v-on="inputEvents.end"
								@click="pickDate = true"
							/>
							<span class="chip__label popup__input-text">Выезд</span>
						</label>
					</template>
				</v-date-picker>

				<button v-if="!pickDate" @click="sendDate = true" class="btn">отправить запрос</button>
				<button v-else :disabled="!dateStart.end" @click="pickDate = false" class="btn">подтвердить</button>
			</div>
			<div v-else>
				<p class="chip__text chip__text--checkIn">
					Ваша заявка оформлена, ожидайте звонок из отеля в ближайшее время!
				</p>
				<button @click="popups[7] = false" class="btn">ЗАКРЫТЬ</button>
			</div>

		</template>
	</info-popup>

	<info-popup class="score" :show="popups[8]" @closeInfoPopup="closeInfoPopup(8)" closeBtn="Закрыть">
		<template v-slot:header>
			<div class="score__header"><p>Ваши баллы: <span>100</span></p></div>
			<p></p>
		</template>

		<template v-slot:content>
			<p class="chip__text chip__text--score">
				Накопленные баллы можно обменять на бесплатную ночь в отеле, получить аккредитацию на обед, скидку на спа-процедуры и другое.
			</p>
			<ul class="score__list">
					<h3 class="chip__text chip__text--sm">Баллы начисляются за:</h3>
					<li class="score__item chip__text chip__text--sm">проживание в любой из гостиниц сети или в одном из 18 её дочерних брендов </li>
